import Layout from "../components/Layout";

import styled from "styled-components";
import NextSeo from "next-seo";

import { Row, Col, Container } from "reactstrap";

import imgIntro from "../static/images/RMBackofBusinessCard.png";
import imgIsAbout from "../static/images/imgIntro.jpg";
import imgMovie from "../static/images/RevelerLogo.png";
import imgHaystacks from "../static/images/roughneckProfilePic.jpg";
import imgMrsEating from "../static/images/heatherSelfie.jpg";
import imgDancing from "../static/images/originalBandPhoto.jpg";
import imgSelfie from "../static/images/deucesWild.jpg";

const AboutDiv = styled.div`
  margin: 0;
  padding: 0;
  height: 83vh;
  background: white;

  .rowWrapper {
    margin: 0;
    padding: 0;
  }

  .row {
    margin: 0;
    padding: 0;
  }

  h1 {
    font-family: "Cookie", cursive;
    font-size: 4.5vw;
    text-align: center;
  }

  h2 {
    font-family: "Permanent Marker", cursive;
    font-size: 2vw;
    text-align: center;
  }

  p {
    font-family: "Special Elite", cursive;
    font-size: 1vw;
    text-align: justify;
  }

  img {
    width: 10vw;
    float: right;
    margin: 0.5vw;
  }

  #imgIntro {
    width: 21vw;
    float: left;
  }

  #imgIsAbout {
    width: 8vw;
    float: left;
  }

  #imgMovie {
    width: 15vw;
    float: right;
  }

  #imgHaystacks {
    width: 10vw;
    float: left;
    outline: 0.15vw dashed black;
    border: 0.4vw solid white;
  }

  #imgMrsEating {
    width: 9vw;
    float: right;
    margin-top: 0.3vw;
    outline: 0.15vw dashed black;
    border: 0.4vw solid white;
  }
`;

function About() {
  return (
    <Layout>
      <AboutDiv>
        <NextSeo
          config={{
            title: "Reveler Media Services Next.js/Firebase starter",
            description:
              "Next.js starter template with Styled-Components, Firebase, Next-SEO"
          }}
        />
        <h1>About Reveler Music, LLC</h1>
        <div className="rowWrapper">
          <Row>
            <Col>
              <h2>Meet Reveler</h2>
              <p>
                Most work very hard each and every day to provide for himself or{" "}
                <img
                  src={imgIntro}
                  id="imgIntro"
                  alt="Santa & the Mrs of West Texas"
                />
                herself and his or her family. With all of this struggle and
                sacrifice it is essential that the important days go well. You
                deserve to have your special events such as a daughter or son's
                wedding or his or her college graduation make you smile. Reveler
                Music wants to remove all worries one has about keeping the ones
                you love happy and having a good time.
              </p>
            </Col>
            <Col>
              <h2>The Reveler Process</h2>
              <p>
                In order to ensure 100% complete and total being happy and
                <img
                  src={imgMovie}
                  alt="Santa & the Mrs at the Odessa Movie Theatre"
                  id="imgMovie"
                />{" "}
                having a good time for you and your loved ones, Reveler Music,
                LLC created a 4 step Experience Guarantee Process. 1) You and
                Reveler will go through the Entertainment service options
                available, and together we will find the right option. 2) Next,
                together we will walk through the event and visualize the best
                outcome. 3) In the next step of the process, Reveler implements
                your vision in a way that ensures 100% complete and total
                satisfaction. No excuses! 4) Finally, the last and the best
                step, You go Party down, and enjoy what you have earned.
              </p>
            </Col>
            <Col>
              <h2>J.</h2>
              <p>
                Although able to complete college degrees in Accounting,
                Business Administration and Information Architecture, J
                maintains the blue-collar work ethic he learned growing up in
                Odessa Texas. From the oil rigs of West Texas to the board rooms
                of the largest tax accounting firm in the world, J has
                appreciated a diverse work history, as well as, a diverse
                understanding and tolerance of people and of cultures. Having
                played the guitar and having sang in bands from Death Metal to
                Country music for more than 30 years, J has an extensive
                background music entertainment.
                <img
                  src={imgHaystacks}
                  id="imgHaystacks"
                  alt="Santa Baking Cookies"
                />{" "}
                and enjoying time with the Mrs. Little known fact about Santa is
                he not only enjoys eating cookies, but Santa also loves to bake
                cookies. If one is really lucky and good, he or she may be
                treated with one of Santa's amazing Buck Eyes or maybe one of
                His nummy Haystacks or Special Ks or the perfectly fluffy peanut
                brittle.
              </p>
            </Col>
            <Col>
              <h2>Heather</h2>
              <p>
                With a deep seated faith in God and with a passionate&nbsp;
                <img
                  src={imgMrsEating}
                  id="imgMrsEating"
                  alt="the Mrs Enjoying Barbeque"
                />{" "}
                loyatly to the Permian Basin, the Mrs has dedicated her life to
                the caring and to the nuturing of others. the Mrs raised two
                outstanding and successful children while devoting herself to
                bringing new life to the world as an OBGYN nurse at Women's and
                Children's Hospital in Odessa. Now retired the Mrs spends most
                of her time fattening up Santa and playing with the reindeer.
              </p>
            </Col>
          </Row>
        </div>
      </AboutDiv>
    </Layout>
  );
}

export default About;
